import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getTaxes, deleteTaxes } from '../../api/misc';
import { getTaxesSearchFilterObject } from '../../helpers/getSearchFilterObject';
import TaxesFilter from '../../App/components/SearchFilters/TaxesFilter';
import { getStartEnd } from '../../helpers/getStartEnd';
import { deleteAccountTypes, getAccountTypes } from '../../api/accounting/account';

const AccountTypes = (props) => {
    const [accountTypesArr, setAccountTypesArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, accountTypeId: null });
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {

        const controller = new AbortController()

        getAccountTypes({
            "action": "get",
            "filter": "none",
            ...getStartEnd(props)
        }, controller.signal)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    console.log(res.json.account_types)
                    setAccountTypesArr(res.json.account_types);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, accountTypeId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteAccountTypes({ action: 'delete', account_type_ids: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deleted = accountTypesArr.find(instance => instance.id === idToBeDeleted);
                    let tempArr = accountTypesArr;
                    tempArr.splice(tempArr.indexOf(deleted), 1);
                    await setAccountTypesArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, accountTypeId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Account type deleted successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const TaxesList = accountTypesArr.map((instance, index) => {
        return (
            <tr key={index}>
                <th scope="row">{instance.id}</th>
                <td>{instance.name[0].toUpperCase() + instance.name.slice(1)}</td>
                <td>{instance.header}</td>
                <td>{instance.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/account-type/update/' + instance.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(instance.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Account Types</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`account-type/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Header</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {TaxesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(accountTypesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />

                        </Card.Body>
                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.accountTypeId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default AccountTypes;